"use client"

import { useState, useEffect } from "react"
import { useParams } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Separator } from "@/components/ui/separator"
import { Skeleton } from "@/components/ui/skeleton"
import type { Job } from "@/app/api/jobs/route"

interface JobsResponse {
  success: boolean
  jobs: Job[]
  total: number
  error?: string
}

export default function JobDetailsPage() {
  const params = useParams()
  const jobId = params.jobId as string
  const [job, setJob] = useState<Job | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)
  const [applying, setApplying] = useState(false)

  useEffect(() => {
    const fetchJob = async () => {
      setLoading(true)
      setError(null)
      
      try {
        const response = await fetch('/api/jobs')
        const data: JobsResponse = await response.json()

        if (data.success) {
          const foundJob = data.jobs.find(j => j.id === jobId)
          if (foundJob) {
            setJob(foundJob)
          } else {
            setError('Job not found')
          }
        } else {
          setError(data.error || 'Failed to fetch job details')
        }
      } catch (err) {
        setError('Network error. Please try again.')
        console.error('Error fetching job:', err)
      } finally {
        setLoading(false)
      }
    }

    if (jobId) {
      fetchJob()
    }
  }, [jobId])

  const handleApply = async () => {
    setApplying(true)
    // Simulate application process
    await new Promise(resolve => setTimeout(resolve, 2000))
    setApplying(false)
    alert('Application submitted successfully! The employer will contact you soon.')
  }

  const formatDate = (dateString: string) => {
    const date = new Date(dateString)
    return date.toLocaleDateString('en-ZA', { 
      year: 'numeric', 
      month: 'long', 
      day: 'numeric' 
    })
  }

  if (loading) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-4xl mx-auto">
          <div className="mb-6">
            <Skeleton className="h-8 w-32 mb-4" />
            <Skeleton className="h-12 w-3/4 mb-2" />
            <Skeleton className="h-6 w-1/2 mb-2" />
            <Skeleton className="h-6 w-1/3" />
          </div>
          
          <div className="grid md:grid-cols-3 gap-8">
            <div className="md:col-span-2 space-y-6">
              <Card>
                <CardHeader>
                  <Skeleton className="h-6 w-48" />
                </CardHeader>
                <CardContent>
                  <Skeleton className="h-32 w-full" />
                </CardContent>
              </Card>
              
              <Card>
                <CardHeader>
                  <Skeleton className="h-6 w-32" />
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-3/4" />
                  </div>
                </CardContent>
              </Card>
            </div>
            
            <div>
              <Card>
                <CardHeader>
                  <Skeleton className="h-6 w-32" />
                </CardHeader>
                <CardContent className="space-y-4">
                  <Skeleton className="h-10 w-full" />
                  <Skeleton className="h-4 w-full" />
                  <Skeleton className="h-4 w-3/4" />
                </CardContent>
              </Card>
            </div>
          </div>
        </div>
      </div>
    )
  }

  if (error || !job) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="max-w-2xl mx-auto text-center">
          <h1 className="text-3xl font-bold mb-4">Job Not Found</h1>
          <p className="text-muted-foreground mb-8">
            {error || "The job you're looking for doesn't exist or has been removed."}
          </p>
          <div className="flex gap-4 justify-center">
            <Button asChild>
              <a href="/jobs">Browse All Jobs</a>
            </Button>
            <Button variant="outline" asChild>
              <a href="/">Go Home</a>
            </Button>
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="max-w-4xl mx-auto">
        {/* Header */}
        <div className="mb-8">
          <Button variant="outline" asChild className="mb-4">
            <a href="/jobs">← Back to Jobs</a>
          </Button>
          
          <div className="flex flex-wrap gap-2 mb-4">
            <Badge variant="secondary">{job.type}</Badge>
            <Badge variant="outline">{job.category}</Badge>
          </div>
          
          <h1 className="text-4xl font-bold mb-2">{job.title}</h1>
          <p className="text-xl text-foreground font-medium mb-1">{job.company}</p>
          <p className="text-muted-foreground">{job.location}</p>
        </div>

        <div className="grid md:grid-cols-3 gap-8">
          {/* Main Content */}
          <div className="md:col-span-2 space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Job Description</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-muted-foreground leading-relaxed">
                  {job.description}
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Requirements</CardTitle>
              </CardHeader>
              <CardContent>
                <ul className="space-y-2">
                  {job.requirements.map((requirement, index) => (
                    <li key={index} className="flex items-start">
                      <span className="text-primary mr-2">•</span>
                      <span className="text-muted-foreground">{requirement}</span>
                    </li>
                  ))}
                </ul>
              </CardContent>
            </Card>
          </div>

          {/* Sidebar */}
          <div className="space-y-6">
            <Card>
              <CardHeader>
                <CardTitle>Apply Now</CardTitle>
                <CardDescription>
                  Ready to start your career journey?
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <Button 
                  onClick={handleApply} 
                  disabled={applying}
                  className="w-full"
                  size="lg"
                >
                  {applying ? "Submitting..." : "Apply for this Job"}
                </Button>
                <p className="text-xs text-muted-foreground">
                  By applying, you agree to our terms and conditions. 
                  We'll share your contact details with the employer.
                </p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Job Details</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <p className="font-medium text-sm">Salary</p>
                  <p className="text-primary font-semibold">{job.salary}</p>
                </div>
                
                <Separator />
                
                <div>
                  <p className="font-medium text-sm">Job Type</p>
                  <p className="text-muted-foreground">{job.type}</p>
                </div>
                
                <Separator />
                
                <div>
                  <p className="font-medium text-sm">Category</p>
                  <p className="text-muted-foreground">{job.category}</p>
                </div>
                
                <Separator />
                
                <div>
                  <p className="font-medium text-sm">Posted Date</p>
                  <p className="text-muted-foreground">{formatDate(job.postedDate)}</p>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Share this Job</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground mb-3">
                  Know someone who might be interested?
                </p>
                <Button variant="outline" className="w-full" size="sm">
                  Copy Job Link
                </Button>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}
